@font-face {
  font-family: 'Montserrat';
  src: url(../fonts/montserrat-regular.woff2), url(../fonts/montserrat-regular.woff);
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Montserrat';
  src: url(../fonts/montserrat-bold.woff2), url(../fonts/montserrat-bold.woff);
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
.page {
  font-size: 62.5%;
}
@media (min-width: 1200px) {
  .page {
    font-size: 100%;
  }
}
.page__body {
  max-width: 100%;
  min-width: 360px;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', 'Arial', sans-serif;
  font-size: 1.6rem;
  line-height: 24px;
  font-weight: 400;
  color: #9baacf;
  background: #E4EBF5;
}
.container {
  max-width: 1200px;
  margin: 2rem;
  padding: 20px;
  box-shadow: 0.3rem 0.3rem 0.6rem #c8d0e7, -0.2rem -0.2rem 0.5rem #ffffff;
  border-radius: 1rem;
}
@media (min-width: 1200px) {
  .container {
    margin: 2rem auto;
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}
.caption {
  font-size: 2.4rem;
  font-weight: 700;
  line-height: 30px;
  text-align: center;
  color: #6d5dfc;
}
.about__text {
  text-align: center;
}
@media (min-width: 1200px) {
  .about__text {
    line-height: 35px;
  }
}
.header__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
@media (min-width: 768px) {
  .header__wrapper {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.header__about {
  margin-bottom: 20px;
  font-size: 2rem;
  text-align: center;
}
@media (min-width: 768px) {
  .header__about {
    margin: 0;
    text-align: left;
    align-self: center;
    line-height: 35px;
  }
}
.header__prof {
  font-size: 1.5rem;
  color: #bec8e4;
}
.header__socials {
  width: 250px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .header__socials {
    margin: 0;
  }
}
.social {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.social__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  box-shadow: 0.3rem 0.3rem 0.6rem #c8d0e7, -0.2rem -0.2rem 0.5rem #ffffff;
  border-radius: 50%;
}
.social__link:hover path {
  fill: #6d5dfc;
  transition: all 0.5s ease;
}
.social__link:hover .not-to-fill {
  fill: none;
  stroke: #6d5dfc;
}
.social__link:active {
  box-shadow: inset 0.2rem 0.2rem 0.5rem #c8d0e7, inset -0.2rem -0.2rem 0.5rem #ffffff;
}
@media (min-width: 768px) {
  .hello {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 60px;
  }
}
.hello__wrapper {
  width: 100%;
  max-width: 420px;
  height: auto;
  margin: 0 auto 20px;
}
@media (min-width: 768px) {
  .hello__wrapper {
    display: flex;
    align-items: center;
    margin: 0;
  }
}
@media (min-width: 1200px) {
  .hello__wrapper {
    max-width: 500px;
  }
}
.hello__image {
  width: 100%;
  height: auto;
  object-fit: cover;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .info {
    width: 100%;
    max-width: 350px;
  }
}
@media (min-width: 1200px) {
  .info {
    max-width: 500px;
  }
}
.info__hi {
  width: 150px;
  height: 40px;
  margin: 0 auto 10px;
  line-height: 40px;
  text-align: center;
  box-shadow: 0.3rem 0.3rem 0.6rem #c8d0e7, -0.2rem -0.2rem 0.5rem #ffffff;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .info__hi {
    margin-bottom: 25px;
  }
}
@media (min-width: 1200px) {
  .info__hi {
    width: 220px;
    height: 50px;
    margin-bottom: 30px;
    line-height: 47px;
  }
}
.info__who {
  margin-bottom: 10px;
  padding-bottom: 10px;
  text-align: center;
  border-bottom: 1px solid #c8d0e7;
}
@media (min-width: 768px) {
  .info__who {
    margin-bottom: 20px;
  }
}
@media (min-width: 1200px) {
  .info__who {
    margin-bottom: 25px;
  }
}
.info__name {
  font-weight: 700;
  color: #6d5dfc;
}
.table {
  margin: 0 auto;
  font-size: 1em;
}
.table tr {
  height: 25px;
}
@media (min-width: 768px) {
  .table tr {
    height: 35px;
  }
}
@media (min-width: 768px) {
  .table tr {
    height: 45px;
  }
}
.table__first-column {
  font-weight: 700;
}
.table__second-column {
  padding-left: 15px;
}
@media (min-width: 768px) {
  .table__second-column {
    padding-left: 25px;
  }
}
.table__link {
  color: #9baacf;
}
.table__link:hover {
  color: #6d5dfc;
  text-decoration: none;
  transition: all 0.5s ease;
}
.table__link:active {
  color: #bec8e4;
}
.skills__text-wrapper {
  margin-bottom: 3rem;
}
@media (min-width: 1200px) {
  .skills__text {
    line-height: 35px;
  }
}
.skills__category {
  font-weight: 700;
}
.skills__hard {
  margin-bottom: 1rem;
}
.skills__professional {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  width: 100%;
  max-width: 500px;
  margin: 0 auto 3rem;
}
@media (min-width: 768px) {
  .skills__professional {
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-content: center;
    column-gap: 2rem;
    padding: 0;
    max-width: 750px;
  }
}
@media (min-width: 1200px) {
  .skills__professional {
    max-width: 1000px;
  }
}
.skills__block {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}
.skills__description {
  padding-left: 1.2rem;
}
.skills__level-container {
  width: 100%;
  height: 2rem;
  padding: 0.5rem 1rem;
  border-radius: 1rem;
  box-shadow: 0.3rem 0.3rem 0.6rem #c8d0e7, -0.2rem -0.2rem 0.5rem #ffffff;
}
.skills__scale {
  position: relative;
  display: block;
  width: 100%;
  height: 1rem;
  border-radius: 0.5rem;
  background-color: #6d5dfc;
  box-shadow: 1px 1px 7px #6d5dfc;
}
.skills__scale.skills__scale--html {
  width: 85%;
}
.skills__scale.skills__scale--css {
  width: 80%;
}
.skills__scale.skills__scale--js {
  width: 30%;
}
.skills__scale.skills__scale--php {
  width: 15%;
}
.skills__scale.skills__scale--git {
  width: 20%;
}
